body { 
    /*background-image: url('./assets/background.webp'); Ruta a tu imagen de fondo */ 
    background-size: cover; /* Asegura que la imagen cubra todo el fondo */ 
    background-position: center; /* Centra la imagen de fondo */ 
    background-repeat: no-repeat; /* Evita que la imagen se repita */ 
    background-attachment: fixed; /* La imagen se queda fija cuando se desplaza la página */ 
    color: #000; /* Cambia el color del texto para que sea legible sobre el fondo */ 
  }


  .search-panel {
    margin-top: 48px;
    background-color: #cecece;
    padding: 8px;
  }
  .search-panel__p1 {
    font-size: 16px;
    font-weight: 600;
  }
  .search-panel__p2 {
    font-size: 16px;
    font-weight: 100;
  }

  .search-panel input {
    width: 100%;
    border: 1px solid #c0c0c0;
    border-radius: 8px;
  }