.navbar { 
    width: 100%;
    background-color: #0f5f53; /* Color de la barra */
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar__container {
    width: 80%;
    max-width: 1200px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.navbar-brand {
    font-family: "Alegreya Sans SC", sans-serif;
    color: #fff;
    font-weight: 500;
    text-align: center;
    flex-grow: 1; /* Esto permite que se mantenga centrado */
}
.navbar-brand::after {
    color: #fff;
}

.nav-link {
    color: #fff;
    text-decoration: none;
    font-size: 20px;
    display: flex;
    align-items: center;
}

.cart-link {
    position: relative;
}

.cart-count {
    background-color: #000;
    color: white;
    font-size: 12px;
    font-weight: bold;
    padding: 3px 7px;
    border-radius: 100%;
    position: absolute;
    top: -10px;
    right: -10px;
}